import React, { useState, useEffect } from 'react';

const LogoLoader = () => {
  const [showAdrian, setShowAdrian] = useState(false);
  const [showGisbert, setShowGisbert] = useState(false);

  useEffect(() => {
    // Mostrar "Adrian" letra por letra con intervalo de 200ms
    const showAdrianInterval = setInterval(() => {
      switch (true) {
        case !showAdrian:
          setShowAdrian('A');
          break;
        case showAdrian === 'A':
          setShowAdrian('Ad');
          break;
        case showAdrian === 'Ad':
          setShowAdrian('Adr');
          break;
        case showAdrian === 'Adr':
          setShowAdrian('Adri');
          break;
        case showAdrian === 'Adri':
          setShowAdrian('Adria');
          break;
        case showAdrian === 'Adria':
          setShowAdrian('Adrian');
          clearInterval(showAdrianInterval);
          break;
        default:
          break;
      }
    }, 100);

    // Mostrar "Gisbert" letra por letra con intervalo de 200ms después de "Adrian"
    const showGisbertInterval = setTimeout(() => {
      setShowGisbert('G');
      setTimeout(() => setShowGisbert('Gi'), 200);
      setTimeout(() => setShowGisbert('Gis'), 400);
      setTimeout(() => setShowGisbert('Gisb'), 600);
      setTimeout(() => setShowGisbert('Gisbe'), 800);
      setTimeout(() => setShowGisbert('Gisber'), 1000);
      setTimeout(() => setShowGisbert('Gisbert'), 1200);
    }, 1500);

    return () => {
      clearInterval(showAdrianInterval);
      clearInterval(showGisbertInterval);
    };
  }, [showAdrian]);

  return (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      viewBox="0 0 200 200"
      width="200"
      height="200"
      style={{ display: 'block', margin: 'auto' }}
    >
      <defs>
        <style>{`
          .myHexagon {
            stroke-dasharray: 800;
            stroke-dashoffset: 800;
            animation: dash 4s linear forwards 0.5s;
          }
          .signature {
            stroke-dasharray: 800;
            stroke-dashoffset: 800;
            animation: dash 6s linear forwards 0.5s;
          }
          .letter {
            opacity: 0;
            animation: fadein 1s ease forwards;
          }
          @keyframes dash {
            from {
              stroke-dashoffset: 800;
            }
            to {
              stroke-dashoffset: 0;
            }
          }
          @keyframes fadein {
            0% {
              opacity: 0;
              transform: translateY(10px);
            }
            100% {
              opacity: 1;
              transform: translateY(0);
            }
          }
        `}</style>
      </defs>

      {/* Hexágono */}
      <polygon
        className="myHexagon"
        points="100,20 160,50 160,110 100,140 40,110 40,50"
        fill="none"
        stroke="yellow"
        strokeWidth="1"
      />

      {/* Letra "A" */}
      <text x="80" y="90" textAnchor="middle" fontSize="40" fontFamily="Brush Script MT" fontWeight="bold" fill="white">
        <tspan className={`letter ${showAdrian === 'A' ? 'show' : ''}`}>A</tspan>
      </text>

      {/* Letra "G" */}
      <text x="115" y="90" textAnchor="middle" fontSize="40" fontFamily="Brush Script MT" fontWeight="bold" fill="white">
        <tspan className={`letter ${showAdrian === 'Adrian' ? 'show' : ''}`}>G</tspan>
      </text>

      {/* Texto "Adrian Gisbert" */}
      <text x="100" y="170" textAnchor="middle" fontSize="26" fontFamily="Brush Script MT" fill="white">
        <tspan className={`letter ${showAdrian ? 'show' : ''}`}>{showAdrian}</tspan>{' '}
        <tspan className={`letter ${showGisbert ? 'show' : ''}`}>{showGisbert}</tspan>
      </text>
    </svg>
  );
};

export default LogoLoader;
